export default {
	name: "",
	components: {

	},
	props: {
		videos: {
			type: Object,
			default: () => {}
		}
	},
	data() {
		return {
			playId: ''
		}
	},
	methods: {
		// 开始播放和继续播放时的方法
		startPlay() {
			this.playId = this.videos.id
		}
	},
	mounted() {
		this.videoContext = uni.createVideoContext(this.videos.id); // 获取video上下文对象
	},
	onLoad() {

	},
	filters: {

	},
	computed: {

	},
	watch: {
		// 监听播放的是哪一个
		playId() {
			if (this.playId === '') {
				this.startPlay()
			} else {
				if (this.playId != this.videos.id) {
					this.videoContext.pause()
				} else {
					console.log(this.videoContext);
					this.videoContext.play()
				}
			}
		}
	},
	directives: {

	}
}
